                        value: parseFloat((res[0][i].total)),
                        name: res[0][i].zuozhe
                    })
                }
                let xAxis2 = [];
                let yAxis2 = [];
                let pArray2 = []
                for(let i=0;i<res[1].length;i++){
					if(this.boardBase&&i==this.boardBase.lineNum){
						break;
					}
                    xAxis2.push(res[1][i].zuozhe);
                    yAxis2.push(parseFloat((res[1][i].total)));
                    pArray2.push({
                        value: parseFloat((res[1][i].total)),
                        name: res[1][i].zuozhe
                    })
                }
                let xAxis3 = [];
                let yAxis3 = [];
                let pArray3 = []
                for(let i=0;i<res[2].length;i++){
					if(this.boardBase&&i==this.boardBase.lineNum){
						break;
					}
                    xAxis3.push(res[2][i].zuozhe);
                    yAxis3.push(parseFloat((res[2][i].total)));
                    pArray3.push({
                        value: parseFloat((res[2][i].total)),
                        name: res[2][i].zuozhe
                    })
                }
                let xAxis4 = [];
                let yAxis4 = [];
                let pArray4 = []
                for(let i=0;i<res[3].length;i++){
					if(this.boardBase&&i==this.boardBase.lineNum){
						break;
					}
                    xAxis4.push(res[3][i].zuozhe);
                    yAxis4.push(parseFloat((res[3][i].total)));
                    pArray4.push({
                        value: parseFloat((res[3][i].total)),
                        name: res[3][i].zuozhe
                    })
                }
                var option = {};
				let titleObj = this.line5?this.line5.title:this.line.title
				titleObj.text = '作者'
                const legendObj = this.line5?this.line5.legend:this.line.legend
                legendObj.data = [
					'收藏数',
					'分享数',
					'评论数',
					'播放数',
                ]
				let xAxisObj = this.line5?this.line5.xAxis:this.line.xAxis
				xAxisObj.type = 'category'
				xAxisObj.boundaryGap = false
				xAxisObj.data = xAxis1
				let yAxisObj = this.line5?this.line5.yAxis:this.line.yAxis
				yAxisObj.type = 'value'
				let seriesObj = [
                    {
                        data: yAxis1,
                        type: 'line',
                        name: '收藏数',
                    },
                    {
                        data: yAxis2,
                        type: 'line',
                        name: '分享数',
                    },
                    {
                        data: yAxis3,
                        type: 'line',
                        name: '评论数',
                    },
                    {
                        data: yAxis4,
                        type: 'line',
                        name: '播放数',
                    },
                ]
                for(let i=0;i<seriesObj.length;i++){
				      seriesObj[i] = Object.assign(seriesObj[i] , this.line5?this.line5.series:this.line.series)
                }
				const gridObj = this.line5.grid
				let tooltipObj = {
					trigger: 'axis',
				}
				tooltipObj = Object.assign(tooltipObj , this.line5.tooltip?this.line5.tooltip:{})
                option = {
					backgroundColor: this.line5?this.line5.backgroundColor:this.line.backgroundColor,
					color: this.line5?this.line5.color:this.line.color,
					title: titleObj,
					legend: legendObj,
                    tooltip: tooltipObj,
					xAxis: xAxisObj,
					yAxis: yAxisObj,
					grid: gridObj,
					series: seriesObj
                };
                // 使用刚指定的配置项和数据显示图表。
                minyaoyinyueChart3.setOption(option);
				

                  //根据窗口的大小变动图表
				window.addEventListener('resize', () => {
                    minyaoyinyueChart3.resize();
                },false);
            }
        });
      })
    },






















































  }
};
